@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);

#main-nav {
  height: 50px;
}

.nav-link {
  font-family: 'Oswald', sans-serif;
  color: #00d6d6 !important;
  font-size: larger;
}
.nav-title {
  font-family: 'Oswald', sans-serif;
  color: #fafcfc !important;
  font-size: x-large;
}
.nav-link:hover {
  color: white !important;
}
.logo {
  max-height: 100px;
}
.nav-bg {
  background-color: #002a32 !important;
}
.part {
  max-width: 350px;
}

.part {
  font-family: 'f';
  font-weight: 250;
  display: flex;
  background-color: #25978c;
  align-items: center;
  vertical-align: middle;
  font-size: large;
  width: 199vh;
  font-family: 'normal';
}
.part-2 {
  min-height: 600px;
  background-color:#04272e;
  font-family: 'Oswald', 'sans-serif';
  font-weight: 350;
  display: flex;
  align-items: center;
  vertical-align: middle;
  font-size: larger;
  color: rgb(10, 10, 10);
}



.part-3 {
  min-height: 600px;
  background-color: #d6f0f7;
  font-family: 'Oswald', 'sans-serif';
  font-weight: 850;
  display: flex;
  align-items: center;
  vertical-align: middle;
  font-size: x-large;
  background-image: 
  linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.4),
    rgba(0, 0, 0, 0.9)
  ),
  url(/earthsea/static/media/img3.2a8cdbf4.jpg);
  color: rgb(250, 248, 248);
}

.quote {
  font-family: fantasy !important;
  font-size: large !important;
}

.footer-link {
  color: #00d6d6;
  text-decoration: underline;
}

.footer-link:hover {
  color: white;

}


.RichEditor-root {
  background: #fff;
  border: 1px solid #ddd;
  font-family: 'Georgia', serif;
  font-size: 16px;
  width: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  margin-right: 10%;
  margin-left: 10%;
  min-height: 100vh;
  padding: 15px;
}

.RichEditor-editor {
  border-top: 1px solid #ddd;
  cursor: text;
  font-size: 16px;
  margin-top: 10px;
}

.RichEditor-editor .public-DraftEditorPlaceholder-root,
.RichEditor-editor .public-DraftEditor-content {
  margin: 0 -15px -15px;
  padding: 15px;
}

.RichEditor-editor .public-DraftEditor-content {
  min-height: 100px;
}

.RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}

.RichEditor-editor .RichEditor-blockquote {
  border-left: 5px solid #eee;
  color: rgb(77, 76, 76);
  font-family: 'Hoefler Text', 'Georgia', serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
  background-color: rgba(0, 0, 0, 0.05);
  font-family: 'Inconsolata', 'Menlo', 'Consolas', monospace;
  font-size: 16px;
  padding: 20px;
}

.RichEditor-controls {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px;
  margin-bottom: 5px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.RichEditor-styleButton {
  color: rgb(27, 26, 26);
  cursor: pointer;
  margin-right: 16px;
  padding: 2px 0;
  display: inline-block;
}

.RichEditor-activeButton {
  color: #5890ff;
}
